import db from '@/lib/db';
import { NextResponse } from 'next/server';

export async function PUT(request, { params }) {
    try {
        const id = (await params).id;
        const { name, description, quantity, image_url, user_id } = await request.json();

        if (!user_id) {
            return NextResponse.json({ error: 'user_id is required' }, { status: 400 });
        }

        // Check if user is admin
        const user = db.prepare('SELECT role FROM users WHERE id = ?').get(user_id);
        if (!user || user.role !== 'admin') {
            return NextResponse.json({ error: 'Only admins can update products' }, { status: 403 });
        }

        const result = db.prepare(
            'UPDATE products SET name = ?, description = ?, quantity = ?, image_url = ? WHERE id = ?'
        ).run(name, description, quantity, image_url, id);

        if (result.changes === 0) {
            return NextResponse.json({ error: 'Product not found' }, { status: 404 });
        }

        return NextResponse.json({ id, name, description, quantity, image_url });
    } catch (error) {
        return NextResponse.json({ error: 'Failed to update product' }, { status: 500 });
    }
}

export async function DELETE(request, { params }) {
    try {
        const id = (await params).id;
        const body = await request.json();
        const user_id = body?.user_id;

        if (!user_id) {
            return NextResponse.json({ error: 'user_id is required' }, { status: 400 });
        }

        // Check if user is admin
        const user = db.prepare('SELECT role FROM users WHERE id = ?').get(user_id);
        if (!user || user.role !== 'admin') {
            return NextResponse.json({ error: 'Only admins can delete products' }, { status: 403 });
        }

        console.log(`Attempting to delete product with id: ${id}`);
        
        // First, delete all transactions related to this product
        db.prepare('DELETE FROM transactions WHERE product_id = ?').run(id);
        console.log(`Deleted transactions for product ${id}`);
        
        // Then delete the product
        const result = db.prepare('DELETE FROM products WHERE id = ?').run(id);
        console.log(`Delete result:`, result);

        if (result.changes === 0) {
            return NextResponse.json({ error: 'Product not found' }, { status: 404 });
        }

        return NextResponse.json({ message: 'Product deleted successfully' });
    } catch (error) {
        console.error('Error deleting product:', error);
        return NextResponse.json({ error: 'Failed to delete product', details: error.message }, { status: 500 });
    }
}
